.stars {
  display: flex;
}

.stars__star {
  background: var(--stars-repeat-position) url("../../images/star-active.svg");
  height: 16px;
  width: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 :nth-child(n),
.stars--1 :nth-child(n+2),
.stars--2 :nth-child(n+3),
.stars--3 :nth-child(n+4),
.stars--4 :last-child {
  background: var(--stars-repeat-position) url("../../images/star.svg");
}
